@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap" rel="stylesheet);
html{font-family:'Roboto', sans-serif;font-size:16px}html body{margin:0px;padding:0px}html body .Row-1{display:grid;grid-template-columns:repeat(1, 1fr)}html body .Row-2{display:grid;grid-template-columns:repeat(2, 1fr)}html body .Row-3{display:grid;grid-template-columns:repeat(3, 1fr)}html body .Row-4{display:grid;grid-template-columns:repeat(4, 1fr)}html body .Row-5{display:grid;grid-template-columns:repeat(5, 1fr)}html body .Row-6{display:grid;grid-template-columns:repeat(6, 1fr)}html body .Row-7{display:grid;grid-template-columns:repeat(7, 1fr)}html body .Row-8{display:grid;grid-template-columns:repeat(8, 1fr)}html body .Row-9{display:grid;grid-template-columns:repeat(9, 1fr)}html body .Row-10{display:grid;grid-template-columns:repeat(10, 1fr)}html a{text-decoration:none;color:#222222}html a:hover{color:#003559}html a:active{color:red}html button{background-color:#005e86;border-radius:16px;border:1px solid #005e86;display:inline-block;cursor:pointer;color:#ffffff;font-family:Verdana;font-size:1.2em;padding:6px 20px;text-decoration:none;text-shadow:0px 1px 0px #005e86;margin:1em}html button:hover{background-color:#222}html button:active{position:relative;top:1px}html button a{color:#ffffff}html .Red{background-color:#880000}html body{margin:0;padding:0;display:grid;height:100vh;grid-template-columns:auto 1fr}html body .Column{display:flex;flex-flow:column}html body header{background-color:#005e86;padding:2em 0.5em;color:#ffffff;border-right:2px solid #003559}html body header ul{padding:1em 0.5em 1em 1em;font-size:2em;text-transform:uppercase;list-style-type:none;color:#ffffff}html body header ul li a{color:#ffffff;padding:0.1em 1em;margin:0}html body header ul li a:hover{color:#003559}html body main{background-color:#E8EAF6;padding:0.2em 1em;font-size:1.3em}html body main .Wrapper{padding:0.4em 1em;border-radius:0.6em;margin:0em}html body main .Wrapper .Item{border-radius:0.4em;background-color:rgba(0,94,134,0.12549);margin:2px;padding:5px 10px;text-align:center}html body main .Wrapper .Item div{text-align:center}html body main .Wrapper .Hover:hover{background-color:rgba(0,94,134,0.313725)}html body main .Wrapper .Head{background-color:rgba(0,94,134,0.313725)}html body main .Wrapper .Flex{display:flex;flex-direction:row}html body main .Wrapper .Flex .Item{display:flex;flex-direction:column}html body main .Wrapper form{margin:1em}html body main .Wrapper form input,html body main .Wrapper form select{font-size:1em;padding:0.1em 0.2em;margin:0.5em}html body main .OrdersList-Item{grid-template-columns:30px 1fr 1fr}

